.sub--div {
    width: 86%;
    margin: 5%;
    padding: 3%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    /* background-color: rgba(0, 0, 0, 0.5); */
}

.cer--img {
    width: 100%;
    height: auto;
    border-radius: 2%;
    border: 1px solid white;
    /* box-shadow: 0px 0px 20px whitesmoke; */
}

.title--h1 {
    color: white;
    font-family: 'Tangerine', 'Courier New', Courier, monospace;
    font-size: xx-large;
    text-shadow: 0px 0px 10px cyan;
}

.more--p {
    font-family: 'Times New Roman', Times, serif;
}

.more--btn {
    background-color: rgb(0, 191, 255);
    color: white;
    border: none;
    border-radius: 5px;
    padding: 2%;
}


/*compition certificates */

.cer--img {
    box-shadow: 0px 0px 50px black;
    border: 3px solid black;

}

.sub--cert--div {
    border: none;
    width: 76%;
    align-self: center;
    /* height: 105vmin; */
    /* height: auto; */
}

.main--cert--div {
    border: none;
    border-radius: 0% 0% 10% 10%;
    width: 100%;
    display: flex;
    flex-direction: column;
    box-shadow: 0px -6px 8px black, 10px 0px 30px black;
    padding: 7% 0%;
    margin: 0% 0% 8% 0%;
    /* background-color: white; */
    /* background-image: linear-gradient(90deg,#C6FFDD, #FBD786,#f7797d); */
    -webkit-animation: color-change-2x 4s linear infinite alternate both;
    animation: color-change-2x 4s linear infinite alternate both;
}

@keyframes color-change-2x {
    0% {
        background: #C6FFDD;
    }

    50% {
        background: #FBD786;
    }

    100% {
        background: #f7797d;
    }
}

.title--em {
    text-shadow: 0px 0px 10px black;
    color: black;
    font-weight: 900;
    font-size: xx-large;
}

.heading--h1 {
    /* background-color: white; */
    /* background-image: linear-gradient(125deg,#ff00cc,#333399); */
    -webkit-animation: color-change-2x 4s linear infinite alternate both;
    animation: color-change-2x 4s linear infinite alternate both;
    color: black;
    margin : 0px 0px 20px;
    padding: 10% 0%;
    border-radius: 40% 40% 0% 0%;
    box-shadow: 0px 6px 8px black, 0px -10px 30px black;
    text-shadow: 4px 5px 10px black;
    font-weight: 900;
    font-size: xx-large;
}