.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; 
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

kbd{ 
  margin: 5px;
  font-size: large;
  font-weight: 700;
  font-style: oblique;
  font-family: Georgia, 'Times New Roman', Times, serif;
  padding-bottom: 15px;
  vertical-align: middle;
  padding: 10px 0px;
}
kbd:hover{
  cursor: pointer;
  text-decoration: underline;  
  text-shadow: 5px 5px 30px rgb(248, 248, 248) ;
}  

.Nav-body{  
  position:fixed;
  top: 0px; 
  z-index: 1;
  width: 100%;
  height:auto;
  padding-top: 10px;
  padding-bottom: 1%;
  box-shadow:0px 5px 20px rgb(255, 255, 255);
  background-image: linear-gradient(120deg,black, rgb(18, 17, 65), rgb(68, 16, 9), rgb(70, 64, 10));  
} 

.home-photo{
  box-shadow:5px 5px 40px rgb(0, 0, 0) , -5px 5px 50px rgb(0, 0, 0) , -5px -5px 30px rgb(0, 0, 0) , 5px -5px 15px rgb(0, 0, 0);
}

marquee{
  color: white;
  align-self: center;
  background-color: rgba(2, 23, 23, 0);
  margin: 5px 0px;
  font-family: Georgia, 'Times New Roman', Times, serif;
}



/* ready made from - uiverse.io */
.btn { 
  background: transparent;
  border: none; 
  color: #ffffff;
  text-transform: uppercase;
  position: relative;
  transition: .5s ease; 
}

.btn::before {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  height: 2px;
  width: 0;
  background-color: #ffffff;
  transition: .5s ease;
}

.btn:hover {
  color: rgb(255, 255, 255);
  font-size:small;
  padding: 1% 3%;
  text-shadow: 0px 0px 7px rgb(255, 238, 0);
  border: 2px solid white;
  /* border-bottom: none; */
  transition-delay: .1s;
}

/* .btn:hover::before {
  width: 100%;
} */

.btn::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  height: 0;
  width: 100%;
  transition: .4s ease;
  z-index: -1;
}

.btn:hover::after {
  height: 100%;
  transition-delay: 0.4s;
  color: aliceblue;
}

