 .div--bg {
     background-image: url('../Logos/kd-photo.png');
     height: 230px;
     width: 100%;
     margin : 15px 0px;
     background-repeat: no-repeat;
     background-size: cover;
     box-shadow: 2px 20px 20px rgb(0, 0, 0), 2px -10px 10px rgb(0, 0, 0); 

     -webkit-animation: slide-in-bck-center 0.7s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
	        animation: slide-in-bck-center 0.7s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;

 }

 .div--bg--big {
     height: 600px;
     width: 90%;
     font-size: x-large;
     border-radius: 10px;

     -webkit-animation: slide-in-bck-center 0.7s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
	        animation: slide-in-bck-center 0.7s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;

 }

@-webkit-keyframes slide-in-bck-center {
  0% {
    -webkit-transform: translateZ(600px);
            transform: translateZ(600px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
    opacity: 1;
  }
}
@keyframes slide-in-bck-center {
  0% {
    -webkit-transform: translateZ(600px);
            transform: translateZ(600px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
    opacity: 1;
  }
}





 .first--div--home {
     display: flex;
     justify-content: center;
 }

 .h1--title--home {
     font-weight: 500;
     float: right;
 }

 .name--title--home {
     font-weight: 900;
     font-size: larger;
     font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
     float: right;
     text-shadow: 1px 1px 2px cyan, -1px -1px 2px red;
     color: azure;
 }

 .div--text {
     font-family: 'Gill Sans', 'Gill Sans MT';
     background-color: rgba(0, 0, 0, 0.627);
     vertical-align: middle;
     text-shadow: 2px 2px 5px rgba(192, 245, 232, 0.63), -2px -2px 5px rgba(162, 226, 221, 0.521);
     display: flex;
     flex-direction: column;
     width: 100%;
     height: 100%;
     float: right;
     vertical-align: bottom ;
     text-transform: capitalize;
     font-size: larger;
     font-weight: 800;
     /* color: rgba(11, 189, 243, 0.637); */
     color: rgba(0, 0, 0, 0.795);
 }

marquee{
    color: whitesmoke;
    text-shadow: 0px 0px 5px black; 

}

 .div--qualification {
     padding: 10px 0px;
     padding-top: 15%;
     margin: 0px;
     width: 100%;
     height: auto;
     background-image: linear-gradient(138deg, rgb(253, 194, 0), rgb(255, 0, 234));
     color: white;
     align-self: center;
     border: 2px solid rgb(31, 30, 30);
     border-radius: 20px;
     box-shadow: 2px 2px 10px rgb(255, 255, 255), -2px -2px 10px rgb(255, 255, 255);
 
 }
   

.div--home--main {
    display: flex;
    flex-direction: column;
}

.h1--qualification {
    font-family: 'Courier New', Courier, monospace;
    text-shadow: 1px 1px 1px rgb(137, 202, 202), -1px -1px 1px rgb(196, 121, 121);
    font-weight: 800;
}

.ul--qualification {
    font-family: 'Times New Roman', Times, serif;
    font-style: italic;
    display: flex;
    overflow-x: auto;
    white-space: nowrap;
}

.li {
    /* overflow: visible; */
    width: 100%;
    height: 100px;
    padding: 5%;
    margin: 1% 11% 10% 4%;
    border-radius: 10px;
    background-color: rgba(0, 0, 0, 0.484);
    box-shadow: 0px 0px 11px rgb(214, 206, 206);
}

.scroll {
    float: right;
    margin-right: 5px;
    color: rgb(0, 0, 0);
    font-style: italic;
    font-family: 'Courier New', Courier, monospace;
}

.div--qualification--big {
    margin: 10% 0px;
    padding-top: 4%;
    font-size: x-large;
    font-weight: 900;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.div--home--bio {
    padding: 0px;
    padding-top: 50px; 
    width: 100%;
    height: auto;
    background-color: rgba(0, 0, 0, 0.7);
    color: rgb(255, 255, 255);
    align-self: center;
    border-radius: 25px;
    border: 2px outset black;
    font-family: 'Times New Roman', Times, serif, 'Courier New', Courier, monospace;
    font-style: italic;
    box-shadow: 2px 2px 10px rgb(255, 255, 255), -2px -2px 10px rgb(255, 255, 255);
}

.div--home--skills {
    padding: 5px 0px; 
    width: 100%;
    height: auto;
    color: rgb(255, 255, 255);
    align-self: center;
    padding-top: 50px;
    background-color: rgba(0, 0, 0, 0.7);
    border-radius: 25px;
    border: 2px outset black;
    box-shadow: 2px 2px 10px rgb(255, 255, 255), -2px -2px 10px rgb(255, 255, 255);
    font-family: 'Times New Roman', Times, serif, 'Courier New', Courier, monospace;
    font-style: italic;
}

.div--slider {
    margin: 0px 5%;
    width: 90%;
    padding: 0%;
    /* overflow: auto; */
    /* display: flex; */
}

.btn--prev {
    border: none;
    border-radius: 40px;
    display: flex;
    justify-content: center;
    font-size: 40px;
    background-color: rgba(0, 0, 0, 0.8);
    cursor: pointer;
    position: absolute;
    left: 5%;
    height: 50px;
    margin: 5px;
    width: 50px;
    margin-top: 5%;
    text-shadow: 1px 1px 5px rgb(255, 255, 255), -1px -1px 5px rgb(255, 255, 255);
    border: 2px solid black;
    box-shadow: 1px 1px 5px rgb(255, 255, 255), -1px -1px 5px rgb(255, 255, 255);
    vertical-align: middle;
}

.btn--next {
    height: 50px;
    width: 50px;
    border: none;
    border-radius: 40px;
    display: flex;
    margin: 2px;
    justify-content: center;
    font-size: 40px;
    background-color: rgba(0, 0, 0, 0.8);
    position: absolute;
    right: 5%;
    cursor: pointer;
    margin-top: 5%;
    text-shadow: 1px 1px 5px rgb(255, 255, 255), -1px -1px 5px rgb(255, 255, 255);
    border: 2px solid black;
    box-shadow: 1px 1px 5px rgb(255, 255, 255), -1px -1px 5px rgb(255, 255, 255);
    vertical-align: middle;
}

.disable--btn {
    visibility: hidden;
}

.setbutton {
    display: flex;
    padding: 0px 15px;
    margin-bottom: 5%;
    width: fit-content;
    width: 90%;
}

.set--padding {
    padding-left : 10px;
    width: 100%; 
    font-size: larger;
    font-family:'Cambria, Cochin', Georgia, Times, 'Times New Roman', serif ;
    /* font-family:'cursive' ; */
}

.div--developedBy {
    display: flex;
    height: auto;
    width: 90%;
    align-self: center;
    flex: 2;
}

.div--collageDeveloped {
    width: 50%;
    border-right: 3px solid white;
    padding: 2%; 
}

.div--selfDeveloped {
    width: 50%;
    border-left: 3px dotted white;
    padding: 2%; 
}

.small--margin{
    margin-bottom: 10px;
}

li{
    list-style: square ; 
}






