.footer-main{
    margin-top: 60px;
    width: 100%; 
    color:whitesmoke;
    background-image: linear-gradient(120deg, rgb(28, 26, 110), rgb(68, 16, 9), rgb(70, 64, 10));
    border-radius: 0px 100px 0px 0px;  
    height: 330px; 
    box-shadow: 12px -10px 30px black, 0px 200px 20px black;
}  
.footer{
    padding: 0px 12px;
}
.copyRight:hover{
    cursor:not-allowed;
}
code{
    font-size: 70%;
    display: flex;
    justify-content: center;
}

a{
    text-decoration:none;
    color: white;
    font-size: 13px;
    font-weight: 400; 
}
.logo:hover{
    box-shadow:  2px 2px 10px rgb(0, 0, 0), -2px -2px 10px rgb(0, 0, 0);
}
a:hover{ 
    text-shadow: 2px 2px 10px rgb(0, 0, 0), -2px -2px 10px rgb(255, 255, 255);
    color: aliceblue;
}
sup{
    font-size: 20px;
    font-weight: 500;
}
img{
    width: 30px;
    height: 30px;
}
.social{
    display: flex;
    background-image: linear-gradient( rgb(147, 209, 211), rgb(21, 238, 130) );
    color: black;
    width: 50%; 
    padding: 2px;
    margin: 0px;
    justify-content: center;
    border-radius: 17px;
    transform:translateX(50%) translateY(-40px);  
    box-shadow: 2px 1px 30px white , -2px -2px 30px white; 
} 
.social-small{
    width: 82%;
    padding: 0%;
    margin: 0%;
    transform:translateX(10%) translateY(-40px);  
} 