.main--card{
    width: 90%; 
    margin: 10% 3% 10%;
    padding: 0% 2% 4%;
    background: white;
    color: black;
    border-radius: 15px;
    box-shadow: 0px 0px 10px black;
    display: flex;
    flex-direction: column;
  }
  
  .card {
    height: auto;
    overflow: hidden;
    display: flex;
    padding: 10px 5px; 
  }
  
  iframe{
    height: 31rem;
    border: none;
    border-radius: 10px;
  }

  .circles,
  .browser {
    height: 30px;
    display: flex;
    align-items: center;
  }
  
  .circles {
    width: 20%;
    gap: 8px;
    padding: 10px;
  }
  
  .c {
    width: 09px;
    height: 9px;
    border-radius: 50%;
    margin-bottom: 10px;
    box-shadow: inset 2px 2px 5px rgba(235, 235, 235, 0.356);
  }
  
  .c:first-child {
    background-color: rgb(255,0,0);
  }
  
  .c:nth-child(2) {
    background-color: rgb(255, 251, 0);
  }
  
  .c:last-child {
    background-color: rgb(0, 255, 0);
  }
  
  .browser {
    width: 80%;
    padding: 5px;
    display: flex;
    gap: 10px;
  }
  
  .chevrons {
    display: flex;
    margin-top: 5px;
  }
  
  .search-bar {
    position: relative;
    border: 0.5px solid black;
    border-radius: 5px;
    padding: 5px;
    width: 100%;
    height: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: medium;
    font-weight: 900;
    color: rgb(0, 0, 0);
    box-shadow: inset 2px 2px 2px #05050525;
  } 
  
  .search-bar svg {
    position: absolute;
    left: 10px;
  }